import { IServerCell, IServerDataV3, IServerDiff, IServerPlayer } from '../../common/types/server/types.server.v3';
import { TAdapter } from '../../common/types/types.adapter';
import { IFramerCell, IFramerPlayer, IFramerStep } from '../../common/types/types.framer';

function diffParse(diff: IServerDiff): IFramerStep {
  return {
    x: diff.L % 100,
    y: Math.floor(diff.L / 100),
    cell: cellParse(diff.V),
  };
}

function cellParse(cell: IServerCell): IFramerCell | undefined {
  return cell
    ? {
        event: Math.floor(cell / 1000),
        hive: Math.floor((cell % 1000) / 100),
        food: Math.floor((cell % 100) / 10),
        ant: Math.floor(cell % 10),
      }
    : undefined;
}

function playerAdapter(data: IServerPlayer[]): IFramerPlayer[] {
  return data.map((p) => ({
    Username: p.Username,
    Picture: p.Picture,
    Skin: p.Skin,
    Span: p.Span,
    Version: p.Version,
    FC: p.FC,
    X: p.X,
    Y: p.Y,
    Wealth: p.Wealth,
    Lang: p.Lang,
    Stats: {
      Age: p.Stats.Age,
      Ants: p.Stats.Ants,
      Hive: p.Stats.Hive,
      Errors: p.Stats.Errors
    }
  }));
}

export const adapterV3: TAdapter = (data: IServerDataV3) => {
  return {
    stateMap: { 0: data.Canvas.map((row) => row.map(cellParse)) },
    playerList: playerAdapter(data.Players),
    diffMatrix: data.Frames.map((row) => row.map(diffParse)),
    map: { height: data.MapSettings.Height, width: data.MapSettings.Width, skin: data.MapSettings.Skin },
  };
};
